        </div>
        <unicon class="helpIcon" @click="toggleHowToPlayModal()" name="question-circle" />
        <b-modal size="lg" v-model="howToPlayModal" title="How To Play" ok-only>
            <ul class="howToPlayUl">
                <li>The rules of this game are quite simple.</li>
                <li>Each player will be having a board of 25 squares having numbers 1 to 25.</li>
                <li>The tricky part is that each player's numbers are randomly ordered.</li>
                <li>When the turn comes player should strike off any non striked number, this will be striked in all the other player's board.</li>
                <li>Players objective is to make 5 consecutive squares striked (vertically, horizontally and diagonally) making a long strike</li>
                <li>The first player to get 5 long strikes wins the game.</li>
