<template>
  <div class="popedEmojis" v-if="emojiList">
    <Emoji v-for="(name, index) in emojiList" :key="index" :name="name" />
  </div>
</template>
<script>
import store from "@/store";
import Emoji from "@/components/svg/Emoji";
export default {
  props: {
    playerId: { type: String }
  },
  computed: {
    emojiList: function() {
      return store.state.player.playersEmojis[this.playerId];
    }
  },
  components: {
    Emoji
  }
};
</script>
